body {
  color: var(--main-color-text);
  margin: 0;
  font-family: Roboto, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

input, textarea {
  outline: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

button {
  font-family: inherit;
  display: block;
}

.head {
  z-index: 2;
  width: 100%;
  height: 50px;
  background-color: #8a2be2;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 1px 1px 10px;
}

.search-api {
  height: 30px;
  width: 300px;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
}

.search-api:focus {
  outline: 2px solid #87ceeb;
}

.search-form {
  align-items: center;
  display: flex;
  position: relative;
}

.button-input {
  height: 100%;
  background-color: rgba(220, 220, 220, .616);
  border-color: rgba(0, 0, 0, 0);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  position: absolute;
  right: 0;
}

.button-input:hover {
  cursor: pointer;
}

.info {
  justify-content: center;
  gap: 35px;
  margin-top: 15px;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-text {
  color: rgba(150, 150, 150, .7);
}

.photo-card {
  max-width: 400px;
  box-shadow: 1px 1px 5px 1px gray;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding-top: 20px;
  display: flex;
}

.load-more {
  visibility: visible;
  background-color: #87ceeb;
  border: 0;
  border-radius: 20px;
  margin: 30px auto 20px;
  padding: 10px 20px;
}

.load-more:active {
  background-color: #8a2be2;
}

.load-more:hover {
  transform: scale(1.02);
  box-shadow: 1px 1px 5px;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.01);
}

.gallery__image {
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

